import { Pipe, PipeTransform } from '@angular/core';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';

@Pipe({
  name: 'markdown',
  standalone: true,
  pure: true  // pure 파이프로 설정하여 입력값이 변경될 때만 실행
})
export class MarkdownPipe implements PipeTransform {
  constructor(private sanitizer: DomSanitizer) {}

  transform(value: string): SafeHtml {
    if (!value) return '';

    // 기본 마크다운 변환
    let html = value;

    // 1단계: 선택지 영역을 임시 마커로 보호 (줄바꿈 처리 전에)
    const choiceBlocks: string[] = [];
    let choiceIndex = 0;
    
    // 전투 버튼 변환 [battle]전투 시작[/battle] 또는 [battle:캐릭터1ID:캐릭터2ID]
    html = html.replace(/\[battle\](.*?)\[\/battle\]/g, (match, text) => {
      const placeholder = `◆◆◆BATTLE${choiceIndex}◆◆◆`;
      const battleId = `battle-area-${choiceIndex}`;
      choiceBlocks[choiceIndex] = 
        `<div class="battle-container" id="${battleId}">` +
          `<div class="battle-trigger-button" data-battle-type="select" data-battle-id="${battleId}">` +
            `<span class="battle-icon">⚔️</span>` +
            `<span class="battle-text">${text || '전투 시작'}</span>` +
          `</div>` +
        `</div>`;
      choiceIndex++;
      return placeholder;
    });

    // 특정 캐릭터 전투 [battle:char1Id:char2Id]텍스트[/battle]
    html = html.replace(/\[battle:([^:]+):([^\]]+)\](.*?)\[\/battle\]/g, (match, char1Id, char2Id, text) => {
      const placeholder = `◆◆◆BATTLE${choiceIndex}◆◆◆`;
      const battleId = `battle-area-${choiceIndex}`;
      choiceBlocks[choiceIndex] = 
        `<div class="battle-container" id="${battleId}">` +
          `<div class="battle-trigger-button" data-battle-type="direct" data-char1="${char1Id}" data-char2="${char2Id}" data-battle-id="${battleId}">` +
            `<span class="battle-icon">⚔️</span>` +
            `<span class="battle-text">${text || '전투 시작'}</span>` +
          `</div>` +
        `</div>`;
      choiceIndex++;
      return placeholder;
    });
    
    // 선택지 블록 전체를 찾아서 저장 (언더스코어가 없는 플레이스홀더 사용)
    html = html.replace(/\[choice:(positive|negative|neutral|aggressive|passive)\]"([^"]+)"/g, (match, type, text) => {
      const placeholder = `◆◆◆CHOICE${choiceIndex}◆◆◆`;
      choiceBlocks[choiceIndex] = `<div class="dialogue-choice" data-choice-type="${type}" data-choice-text="${text}">` +
        `<span class="choice-icon">💬</span>` +
        `<span class="choice-text">"${text}"</span>` +
        `<span class="choice-label">${type}</span>` +
        `</div>`;
      choiceIndex++;
      return placeholder;
    });
    
    html = html.replace(/\[choice:(positive|negative|neutral|aggressive|passive)\]「([^」]+)」/g, (match, type, text) => {
      const placeholder = `◆◆◆CHOICE${choiceIndex}◆◆◆`;
      choiceBlocks[choiceIndex] = `<div class="dialogue-choice" data-choice-type="${type}" data-choice-text="${text}">` +
        `<span class="choice-icon">💬</span>` +
        `<span class="choice-text">「${text}」</span>` +
        `<span class="choice-label">${type}</span>` +
        `</div>`;
      choiceIndex++;
      return placeholder;
    });

    // 일반 대화 부분 감지 (선택지가 아닌 일반 대화)
    html = html.replace(/"([^"]+)"/g, '<span class="dialogue" data-type="dialogue">"$1"</span>');
    html = html.replace(/'([^']+)'/g, '<span class="dialogue" data-type="dialogue">\'$1\'</span>');
    html = html.replace(/「([^」]+)」/g, '<span class="dialogue" data-type="dialogue">「$1」</span>');
    html = html.replace(/『([^』]+)』/g, '<span class="dialogue" data-type="dialogue">『$1』</span>');

    // 헤더 변환 (# ~ ######)
    html = html.replace(/^######\s+(.+)$/gm, '<h6>$1</h6>');
    html = html.replace(/^#####\s+(.+)$/gm, '<h5>$1</h5>');
    html = html.replace(/^####\s+(.+)$/gm, '<h4>$1</h4>');
    html = html.replace(/^###\s+(.+)$/gm, '<h3>$1</h3>');
    html = html.replace(/^##\s+(.+)$/gm, '<h2>$1</h2>');
    html = html.replace(/^#\s+(.+)$/gm, '<h1>$1</h1>');

    // 볼드 변환 (**text** 또는 __text__)
    html = html.replace(/\*\*(.+?)\*\*/g, '<strong>$1</strong>');
    html = html.replace(/__(.+?)__/g, '<strong>$1</strong>');

    // 이탤릭 변환 (*text* 또는 _text_)
    html = html.replace(/\*(.+?)\*/g, '<em>$1</em>');
    html = html.replace(/_(.+?)_/g, '<em>$1</em>');

    // 취소선 변환 (~~text~~)
    html = html.replace(/~~(.+?)~~/g, '<del>$1</del>');

    // 코드 블록 변환 (```code```)
    html = html.replace(/```([^`]+)```/g, '<pre><code>$1</code></pre>');

    // 인라인 코드 변환 (`code`)
    html = html.replace(/`([^`]+)`/g, '<code>$1</code>');

    // 링크 변환 ([text](url))
    html = html.replace(/\[([^\]]+)\]\(([^)]+)\)/g, '<a href="$2" target="_blank">$1</a>');

    // 이미지 변환 (![alt](url))
    html = html.replace(/!\[([^\]]*)\]\(([^)]+)\)/g, '<img src="$2" alt="$1" />');

    // 순서 있는 리스트 변환 (1. item)
    html = html.replace(/^(\d+)\.\s+(.+)$/gm, '<li>$2</li>');
    html = html.replace(/(<li>.*<\/li>\n?)+/g, '<ol>$&</ol>');

    // 순서 없는 리스트 변환 (- item 또는 * item)
    html = html.replace(/^[-*]\s+(.+)$/gm, '<li>$1</li>');
    html = html.replace(/(<li>.*<\/li>\n?)+/g, '<ul>$&</ul>');

    // 인용구 변환 (> quote)
    html = html.replace(/^>\s+(.+)$/gm, '<blockquote>$1</blockquote>');

    // 수평선 변환 (--- 또는 ***)
    html = html.replace(/^(---|\*\*\*)$/gm, '<hr>');

    // 줄바꿈 변환 (placeholder 보호)
    html = html.replace(/\n\n/g, '</p><p>');
    html = html.replace(/\n/g, '<br>');

    // 단락으로 감싸기 (placeholder가 있는 라인은 제외)
    if (!html.startsWith('<h') && !html.startsWith('<ul') && !html.startsWith('<ol') && !html.startsWith('<blockquote')) {
      // placeholder가 단독으로 있는 경우 <p>로 감싸지 않도록 처리
      const lines = html.split('</p><p>');
      html = lines.map(line => {
        // placeholder만 있는 라인은 그대로 유지
        if (line.match(/^◆◆◆(CHOICE|BATTLE)\d+◆◆◆$/)) {
          return line;
        }
        return line;
      }).join('</p><p>');
      
      html = '<p>' + html + '</p>';
    }

    // placeholder 주변의 불필요한 태그 제거 (복원 전에)
    html = html.replace(/<p>\s*(◆◆◆(?:CHOICE|BATTLE)\d+◆◆◆)\s*<\/p>/g, '$1');
    html = html.replace(/<br>\s*(◆◆◆(?:CHOICE|BATTLE)\d+◆◆◆)/g, '$1');
    html = html.replace(/(◆◆◆(?:CHOICE|BATTLE)\d+◆◆◆)\s*<br>/g, '$1');

    // 마지막 단계: 선택지 블록 복원
    for (let i = 0; i < choiceBlocks.length; i++) {
      const placeholderChoice = `◆◆◆CHOICE${i}◆◆◆`;
      const placeholderBattle = `◆◆◆BATTLE${i}◆◆◆`;
      
      // 선택지 또는 전투 플레이스홀더 찾기
      if (html.includes(placeholderChoice)) {
        const regex = new RegExp(placeholderChoice.replace(/[.*+?^${}()|[\]\\]/g, '\\$&'), 'g');
        html = html.replace(regex, choiceBlocks[i]);
      } else if (html.includes(placeholderBattle)) {
        const regex = new RegExp(placeholderBattle.replace(/[.*+?^${}()|[\]\\]/g, '\\$&'), 'g');
        html = html.replace(regex, choiceBlocks[i]);
      }
    }

    // 블록 정리: <br>, <p> 태그가 잘못 들어간 경우 제거
    html = html.replace(/<br>\s*(<div class="dialogue-choice")/g, '$1');
    html = html.replace(/(<\/div>)\s*<br>/g, '$1');
    html = html.replace(/<p>\s*(<div class="dialogue-choice")/g, '$1');
    html = html.replace(/(<\/div>)\s*<\/p>/g, '$1');
    html = html.replace(/<br>\s*(<div class="battle-trigger-button")/g, '$1');
    html = html.replace(/<p>\s*(<div class="battle-trigger-button")/g, '$1');

    return this.sanitizer.bypassSecurityTrustHtml(html);
  }
}
