import { Component, Input, Output, EventEmitter, ChangeDetectionStrategy } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ConversionOption } from '../../../shared/interfaces/common.interfaces';

@Component({
  selector: 'app-shared-file-upload',
  standalone: true,
  imports: [CommonModule],
  changeDetection: ChangeDetectionStrategy.OnPush,
  template: `
    <div class="upload-options" [class.mobile]="isMobile">
      <div 
        class="upload-option" 
        *ngFor="let option of options"
        (click)="onOptionSelect(option)"
        [class.mobile-option]="isMobile"
      >
        <div class="option-icon" *ngIf="option.icon">
          <img [src]="option.icon" [alt]="option.title" />
        </div>
        <div class="option-icon" *ngIf="!option.icon">
          <svg viewBox="0 0 24 24" [attr.data-type]="option.id">
            <path [attr.d]="getIconPath(option.id)"/>
          </svg>
        </div>
        <span class="option-text">{{ option.title }}</span>
      </div>
    </div>
  `,
  styles: [`
    .upload-options {
      display: flex;
      gap: 16px;
      flex-wrap: wrap;
    }

    .upload-options.mobile {
      flex-direction: column;
      gap: 0;
    }

    .upload-option {
      display: flex;
      align-items: center;
      padding: 12px 16px;
      border: 1px solid #e5e7eb;
      border-radius: 8px;
      cursor: pointer;
      transition: all 0.2s ease;
      background: #fff;
      min-width: 120px;
    }

    .upload-option:hover {
      border-color: #007acc;
      box-shadow: 0 2px 8px rgba(0, 122, 204, 0.1);
    }

    .upload-option.mobile-option {
      border: none;
      border-bottom: 1px solid #e5e7eb;
      border-radius: 0;
      padding: 16px 20px;
    }

    .upload-option.mobile-option:last-child {
      border-bottom: none;
    }

    .option-icon {
      width: 24px;
      height: 24px;
      margin-right: 12px;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    .option-icon img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }

    .option-icon svg {
      width: 100%;
      height: 100%;
      fill: #374151;
    }

    .option-text {
      font-size: 14px;
      font-weight: 500;
      color: #374151;
    }

    .mobile .option-text {
      font-size: 16px;
    }
  `]
})
export class SharedFileUploadComponent {
  @Input() options: ConversionOption[] = [];
  @Input() isMobile: boolean = false;
  
  @Output() optionSelect = new EventEmitter<ConversionOption>();

  onOptionSelect(option: ConversionOption): void {
    this.optionSelect.emit(option);
  }

  getIconPath(optionId: string): string {
    const iconPaths = {
      'file': 'M6,2A2,2 0 0,0 4,4V20A2,2 0 0,0 6,22H18A2,2 0 0,0 20,20V8L14,2H6Z',
      'photo': 'M8.5,13.5L11,16.5L14.5,12L19,18H5M21,19V5C21,3.89 20.1,3 19,3H5A2,2 0 0,0 3,5V19A2,2 0 0,0 5,21H19A2,2 0 0,0 21,19Z',
      'pdf': 'M19,3H5C3.89,3 3,3.89 3,5V19A2,2 0 0,0 5,21H19A2,2 0 0,0 21,19V5C21,3.89 20.1,3 19,3M19,5V19H5V5H19Z',
      'image': 'M8.5,13.5L11,16.5L14.5,12L19,18H5M21,19V5C21,3.89 20.1,3 19,3H5A2,2 0 0,0 3,5V19A2,2 0 0,0 5,21H19A2,2 0 0,0 21,19Z',
      'excel': 'M14,2H6A2,2 0 0,0 4,4V20A2,2 0 0,0 6,22H18A2,2 0 0,0 20,20V8L14,2Z',
      'powerpoint': 'M14,2H6A2,2 0 0,0 4,4V20A2,2 0 0,0 6,22H18A2,2 0 0,0 20,20V8L14,2Z',
      'word': 'M14,2H6A2,2 0 0,0 4,4V20A2,2 0 0,0 6,22H18A2,2 0 0,0 20,20V8L14,2Z'
    };
    return iconPaths[optionId as keyof typeof iconPaths] || iconPaths['file'];
  }
}
