import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ThemeService } from '../../../../services/theme.service';
import { takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';

export interface LoginConfig {
  title: string;              // 로그인 타이틀 (예: "문서 에디터", "스토어", "ERP")
  subtitle?: string;          // 부제목
  logoUrl?: string;           // 로고 이미지 경로
  showSocialLogin?: boolean;  // 소셜 로그인 표시 여부
  showRegisterLink?: boolean; // 회원가입 링크 표시 여부
  moduleName: string;         // 모듈명 (로깅용)
  backgroundAnimation?: string[]; // 배경 애니메이션 이모지 배열 (예: ['📖', '📚', '✨'])
  themeColors?: {             // 모듈별 테마 색상
    primary: string;          // 주 색상 (예: '#667eea')
    secondary: string;        // 보조 색상 (예: '#764ba2')
    accent?: string;          // 강조 색상 (선택사항, 예: '#f093fb')
  };
}

@Component({
  selector: 'app-unified-login',
  templateUrl: './unified-login.component.html',
  styleUrls: ['./unified-login.component.scss'],
  standalone: false
})
export class UnifiedLoginComponent implements OnInit {
  @Input() config: LoginConfig = {
    title: '로그인',
    moduleName: 'default'
  };
  
  @Output() loginSubmit = new EventEmitter<{ username: string; password: string }>();
  @Output() socialLogin = new EventEmitter<string>(); // 소셜 로그인 제공자
  @Output() registerClick = new EventEmitter<void>();
  
  loginForm!: FormGroup;
  hidePassword = true;
  private _isLoading = false;
  errorMessage = '';
  isDarkMode = false;
  
  private destroy$ = new Subject<void>();

  // isLoading getter/setter로 FormControl 상태 관리
  get isLoading(): boolean {
    return this._isLoading;
  }

  set isLoading(value: boolean) {
    this._isLoading = value;
    this.updateFormControlsState(value);
  }

  constructor(
    private fb: FormBuilder,
    private themeService: ThemeService
  ) {}

  ngOnInit(): void {
    this.initForm();
    this.subscribeToTheme();
    this.applyThemeColors();
  }

  private applyThemeColors(): void {
    if (this.config.themeColors) {
      const root = document.documentElement;
      root.style.setProperty('--login-primary', this.config.themeColors.primary);
      root.style.setProperty('--login-secondary', this.config.themeColors.secondary);
      root.style.setProperty('--login-accent', this.config.themeColors.accent || this.config.themeColors.secondary);
    }
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  private initForm(): void {
    this.loginForm = this.fb.group({
      username: ['', [Validators.required, Validators.minLength(3)]],
      password: ['', [Validators.required, Validators.minLength(4)]]
    });
  }

  private subscribeToTheme(): void {
    this.themeService.currentTheme$
      .pipe(takeUntil(this.destroy$))
      .subscribe(theme => {
        this.isDarkMode = theme === 'dark';
      });
  }

  onSubmit(): void {
    if (this.loginForm.invalid) {
      this.markFormGroupTouched(this.loginForm);
      return;
    }

    this.isLoading = true;
    this.errorMessage = '';

    const { username, password } = this.loginForm.value;
    this.loginSubmit.emit({ username, password });
  }

  onSocialLogin(provider: string): void {
    this.socialLogin.emit(provider);
  }

  onRegisterClick(): void {
    this.registerClick.emit();
  }

  // 외부에서 에러 메시지 설정
  setError(message: string): void {
    this.errorMessage = message;
    this.isLoading = false;
  }

  // 외부에서 로딩 상태 설정
  setLoading(loading: boolean): void {
    this.isLoading = loading;
  }

  // 폼 초기화
  resetForm(): void {
    this.loginForm.reset();
    this.errorMessage = '';
    this.isLoading = false;
  }

  // 필드 에러 체크
  isFieldInvalid(fieldName: string): boolean {
    const field = this.loginForm.get(fieldName);
    return !!(field && field.invalid && (field.dirty || field.touched));
  }

  // 필드 에러 메시지
  getFieldError(fieldName: string): string {
    const field = this.loginForm.get(fieldName);
    if (!field || !field.errors) return '';

    if (field.errors['required']) {
      return fieldName === 'username' ? '아이디를 입력하세요' : '비밀번호를 입력하세요';
    }
    if (field.errors['minlength']) {
      const minLength = field.errors['minlength'].requiredLength;
      return fieldName === 'username' 
        ? `아이디는 최소 ${minLength}자 이상이어야 합니다`
        : `비밀번호는 최소 ${minLength}자 이상이어야 합니다`;
    }
    return '';
  }

  private markFormGroupTouched(formGroup: FormGroup): void {
    Object.keys(formGroup.controls).forEach(key => {
      const control = formGroup.get(key);
      control?.markAsTouched();
    });
  }

  // FormControl의 enable/disable 상태 업데이트
  private updateFormControlsState(disabled: boolean): void {
    if (!this.loginForm) return;

    if (disabled) {
      this.loginForm.get('username')?.disable({ emitEvent: false });
      this.loginForm.get('password')?.disable({ emitEvent: false });
    } else {
      this.loginForm.get('username')?.enable({ emitEvent: false });
      this.loginForm.get('password')?.enable({ emitEvent: false });
    }
  }
}
