import { 
  ChangeDetectorRef, 
  Component, 
  ComponentRef, 
  ElementRef, 
  EventEmitter, 
  HostListener,
  Input, 
  OnInit, 
  OnDestroy,
  Output, 
  SimpleChanges, 
  ViewChild, 
  ViewContainerRef,
  signal,
  computed,
  AfterContentInit
} from '@angular/core';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';
import { CdkDrag } from '@angular/cdk/drag-drop';
import { animate, state, style, transition, trigger } from '@angular/animations';
import { timer } from 'rxjs';

export interface IconMetaData {
  component: any;
  icon: string;
  label: string;
  route?: string;
}

@Component({
  selector: 'app-window-modal',
  templateUrl: './window-modal.component.html',
  styleUrls: ['./window-modal.component.scss'],
  standalone: false,
  animations: [
    trigger('modalAnimation', [
      state('void', style({ opacity: 0, transform: 'scale(0.3)'})),
      state('*', style({ opacity: 1, transform: 'scale(1)' })),
      transition('void => *', [
        animate('0.2s ease-out')
      ]),
      transition('* => void', [
        animate('0.2s ease-in')
      ])
    ])
  ]
})
export class WindowModalComponent implements OnInit, OnDestroy, AfterContentInit {
  @Input() id!: string;
  @Input() zIndex!: number;
  @Input() metaData!: IconMetaData;
  @Input() folderType?: 'calendar' | 'note'; // 폴더 타입 정보
  @Input() isMobileApp: boolean = false; // 모바일 앱 스타일 적용 여부
  @Input() set zoomLevel(value: number) {
    this.zoomLevelSignal.set(value);
  }
  get zoomLevel(): number {
    return this.zoomLevelSignal();
  }
  @Output() moveToFront = new EventEmitter<string>();
  @Output() close = new EventEmitter<string>();
  @Output() minimizeWindow = new EventEmitter<string>();
  @Output() windowStyleChange = new EventEmitter<{ top: number; left: number }>();

  // Signal으로 줌 레벨 관리
  zoomLevelSignal = signal<number>(100);
  
  // computed로 transform 값 자동 계산
  zoomTransform = computed(() => {
    const scale = this.zoomLevelSignal() / 100;
    return `scale(${scale})`;
  });

  @ViewChild('container', {read: ElementRef, static: false})
  container!: ElementRef;

  @ViewChild(CdkDrag, {static: false})
  cdkDrag!: CdkDrag;

  @ViewChild('containerBody', {read: ViewContainerRef, static: false})
  containerBody!: ViewContainerRef;

  componentRef!: ComponentRef<any> | null;
  iframeUrl: SafeResourceUrl | null = null;

  isMaximized = false;
  windowStyle: any = {
    position: 'absolute',
    width: '900px',
    height: '700px',
    top: `${Math.floor(Math.random() * 100) + 50}px`,
    left: `${Math.floor(Math.random() * 200) + 100}px`,
    display: 'flex',
    flexDirection: 'column',
    boxShadow: '0px 10px 30px rgba(0, 0, 0, 0.3)',
    borderRadius: '12px',
    overflow: 'hidden'
  };

  // 포인터 드래그 처리
  private isDragging = false;
  private dragStartX = 0;
  private dragStartY = 0;
  private elementStartX = 0;
  private elementStartY = 0;
  private activePointerId: number | null = null;

  constructor(
    private cdr: ChangeDetectorRef,
    private sanitizer: DomSanitizer
  ) {
    // 뷰포트 기반 초기 크기 설정
  }

  ngAfterContentInit(): void {
    console.log('Zoom Level after content init:', this.zoomLevel);
  }
  

  ngOnInit(): void {
    if (!this.metaData) {
      return;
    }

    timer(1000).subscribe(() => {
      console.log('Zoom Level on Init:', this.zoomLevel);
     this.zoomLevelSignal.set(this.zoomLevel+1);
    });
  

    // 모바일 앱이면 스마트폰 사이즈로 조정
    if (this.isMobileApp) {
      this.windowStyle = {
        ...this.windowStyle,
        width: '393px',
        height: '852px',
        borderRadius: '10px'
      };
    } else {
      // PC 모달은 초기 줌 레벨을 기반으로 크기 설정
      const baseWidth = 1200;
      const baseHeight = 900;
      this.windowStyle = {
        ...this.windowStyle,
        width: `${Math.round(baseWidth)}px`,
        height: `${Math.round(baseHeight)}px`
      };
    }

    // 컴포넌트가 있으면 동적으로 로드 (우선순위 1)
    if (this.metaData.component) {
      this.containerBody.clear();
      this.componentRef = this.containerBody.createComponent(this.metaData.component);
      // folderType이 있으면 컴포넌트에 전달
      if (this.folderType && this.componentRef.instance) {
        this.componentRef.instance.folderType = this.folderType;
      }
    }
    // 라우트가 있으면 iframe URL 생성 (우선순위 2)
    else if (this.metaData.route) {
      const baseUrl = window.location.origin + window.location.pathname;
      const iframeUrlString = `${baseUrl}#${this.metaData.route}`;
      this.iframeUrl = this.sanitizer.bypassSecurityTrustResourceUrl(iframeUrlString);
    }

    // 초기 윈도우 위치를 부모에 전달
    const topValue = parseInt(this.windowStyle.top, 10);
    const leftValue = parseInt(this.windowStyle.left, 10);
    this.windowStyleChange.emit({
      top: topValue,
      left: leftValue
    });
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes['zIndex']) {
      this.windowStyle = {
        ...this.windowStyle,
        zIndex: this.zIndex
      };
      this.cdr.detectChanges();
    }
  }

  onClick(event: MouseEvent) {
    this.moveToFront.emit(this.id);
  }

  minimize(event: MouseEvent) {
    event.stopPropagation();
    this.minimizeWindow.emit(this.id);
  }

  onContainerWheel(event: WheelEvent): void {
    // 모달 내 스크롤 이벤트가 부모로 전파되지 않도록 차단
    event.stopPropagation();
  }

  maximize() {
    if (this.isMaximized) {
      // 원래 크기로 복원
      if (this.isMobileApp) {
        this.windowStyle = {
          ...this.windowStyle,
          position: 'absolute',
          width: '393px',
          height: '852px',
          top: '50px',
          left: 'calc(50% - 196.5px)',
          borderRadius: '10px'
        };

        this.windowStyleChange.emit({
          top: 50,
          left: 0
        });
      } else {
        // PC 모달은 줌 레벨을 적용하여 복원
        const baseWidth = 900;
        const baseHeight = 700;
        this.windowStyle = {
          ...this.windowStyle,
          position: 'absolute',
          width: `${Math.round(baseWidth)}px`,
          height: `${Math.round(baseHeight)}px`,
          top: '50px',
          left: '100px',
          borderRadius: '12px'
        };

        this.windowStyleChange.emit({
          top: 50,
          left: 100
        });
      }
      this.cdkDrag._dragRef.setFreeDragPosition({ x: 0, y: 0 });
    } else {
      // 전체 화면으로 (메뉴바 포함)
      // 모바일 앱이면 넓이 유지 (393px), PC는 전체화면
      if (this.isMobileApp) {
        this.windowStyle = {
          ...this.windowStyle,
          position: 'fixed',
          top: '0px',
          left: 'calc(50% - 196.5px)',
          transform: 'none',
          width: '393px',
          height: '100vh',
          borderRadius: '10px',
          zIndex: 10001
        };

        this.windowStyleChange.emit({
          top: 0,
          left: 0
        });
      } else {
        this.windowStyle = {
          ...this.windowStyle,
          position: 'fixed',
          top: '0px',
          left: '0px',
          transform: 'none',
          width: '100vw',
          height: '100vh',
          borderRadius: '0',
          zIndex: 10001 // 메뉴바(10000)보다 높게
        };

        this.windowStyleChange.emit({
          top: 0,
          left: 0
        });
      }

      this.cdkDrag._dragRef.reset();
      this.cdkDrag._dragRef.setFreeDragPosition({ x: 0, y: 0 });
    }

    this.isMaximized = !this.isMaximized;
  }

  closeWindow() {

    if (this.componentRef) {
      this.componentRef.destroy();
      this.componentRef = null;
    }

    if (this.containerBody && this.containerBody.length > 0) {
      this.containerBody.detach(0);
    }

    this.close.emit(this.id);
  }

  /**
   * 포인터 다운 이벤트: 드래그 시작
   * 헤더 또는 노치 영역에서만 드래그 가능
   */
  @HostListener('pointerdown', ['$event'])
  onPointerDown(event: PointerEvent): void {
    if (!this.isDragTarget(event.target as HTMLElement)) {
      return;
    }

    event.preventDefault();
    event.stopPropagation();

    this.isDragging = true;
    this.dragStartX = event.clientX;
    this.dragStartY = event.clientY;
    this.activePointerId = event.pointerId;

    const modalElement = this.container.nativeElement as HTMLElement;
    const rect = modalElement.getBoundingClientRect();
    this.elementStartX = rect.left;
    this.elementStartY = rect.top;

    // 포인터 캡처로 iframe을 뚫고 계속 추적
    (event.target as HTMLElement).setPointerCapture(event.pointerId);

    // 전면 상태로 이동
    this.moveToFront.emit(this.id);

    // 글로벌 이벤트 리스너 등록 (iframe 초과 추적)
    document.addEventListener('pointermove', this.onPointerMove, true);
    document.addEventListener('pointerup', this.onPointerUp, true);
  }

  /**
   * 포인터 무브 이벤트: 윈도우 위치 업데이트
   * 바인딩된 화살표 함수로 정의하여 this 컨텍스트 유지
   */
  private onPointerMove = (event: PointerEvent): void => {
    if (!this.isDragging || this.activePointerId !== event.pointerId) {
      return;
    }

    event.preventDefault();
    event.stopPropagation();

    // 마우스 움직임 거리 계산
    const deltaX = event.clientX - this.dragStartX;
    const deltaY = event.clientY - this.dragStartY;

    // 새로운 위치 계산
    const newLeft = this.elementStartX + deltaX;
    const newTop = this.elementStartY + deltaY;

    // 윈도우 스타일 업데이트
    this.windowStyle = {
      ...this.windowStyle,
      left: newLeft + 'px',
      top: newTop + 'px'
    };

    // 부모 컴포넌트에 위치 변경 통보
    this.windowStyleChange.emit({
      top: newTop,
      left: newLeft
    });

    // 변경 감지 트리거
    this.cdr.markForCheck();
  };

  /**
   * 포인터 업 이벤트: 드래그 종료
   */
  private onPointerUp = (event: PointerEvent): void => {
    if (!this.isDragging || this.activePointerId !== event.pointerId) {
      return;
    }

    event.preventDefault();
    event.stopPropagation();

    this.isDragging = false;
    this.activePointerId = null;

    // 포인터 캡처 해제
    if (this.container) {
      const modalElement = this.container.nativeElement as HTMLElement;
      try {
        modalElement.releasePointerCapture(event.pointerId);
      } catch (e) {
        // 무시 - 이미 해제된 경우
      }
    }

    // 글로벌 이벤트 리스너 제거
    document.removeEventListener('pointermove', this.onPointerMove, true);
    document.removeEventListener('pointerup', this.onPointerUp, true);
  };

  /**
   * 드래그 대상 확인 (헤더 또는 모바일 노치)
   */
  private isDragTarget(target: HTMLElement): boolean {
    if (!target) return false;
    
    // 대상 또는 부모가 드래그 핸들인지 확인
    let element: HTMLElement | null = target;
    while (element && element !== this.container?.nativeElement) {
      if (element.classList.contains('container-header') || 
          element.classList.contains('mobile-notch')) {
        return true;
      }
      element = element.parentElement;
    }
    
    return false;
  }

  /**
   * 컴포넌트 소멸 시 이벤트 리스너 정리
   */
  ngOnDestroy(): void {
    // 드래그 중이면 상태 정리
    if (this.isDragging) {
      this.isDragging = false;
      this.activePointerId = null;
      document.removeEventListener('pointermove', this.onPointerMove, true);
      document.removeEventListener('pointerup', this.onPointerUp, true);
    }
  }
}
