import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

export interface TextEditorConfig {
  id: string;
  x: number;
  y: number;
  text: string;
  fontSize: number;
  fontFamily: string;
  color: string;
  bold: boolean;
  italic: boolean;
  underline: boolean;
  width?: number;
  height?: number;
  rotation?: number;
  opacity?: number;
  pageNumber?: number;
}

@Component({
  selector: 'app-pdf-text-modifier',
  templateUrl: './pdf-text-modifier.component.html',
  styleUrls: ['./pdf-text-modifier.component.scss'],
  standalone: false
})
export class PdfTextModifierComponent implements OnInit {
  @Input() isVisible: boolean = false;
  @Input() editors: TextEditorConfig[] = [];
  @Input() activeEditorId: string | null = null;

  @Output() editorsChanged = new EventEmitter<TextEditorConfig[]>();
  @Output() activeEditorChanged = new EventEmitter<string | null>();
  @Output() applyTexts = new EventEmitter<TextEditorConfig[]>();
  @Output() visibilityChanged = new EventEmitter<boolean>();

  selectedEditor: TextEditorConfig | null = null;
  
  // 폰트 옵션들
  fontFamilies = [
    'Arial', 'Arial Black', 'Times New Roman', 'Courier New',
    'Helvetica', 'Georgia', 'Verdana', 'Comic Sans MS'
  ];
  
  fontSizes = [8, 9, 10, 11, 12, 14, 16, 18, 20, 22, 24, 28, 32, 36, 48, 72];

  constructor() {}

  ngOnInit(): void {}

  ngOnChanges(): void {
    if (this.activeEditorId) {
      this.selectedEditor = this.editors.find(e => e.id === this.activeEditorId) || null;
    } else {
      this.selectedEditor = null;
    }
  }

  onClose(): void {
    this.isVisible = false;
    this.visibilityChanged.emit(false);
  }

  addTextEditor(): void {
    const newEditor: TextEditorConfig = {
      id: `text-editor-${Date.now()}`,
      x: 100,
      y: 100,
      text: '새 텍스트',
      fontSize: 14,
      fontFamily: 'Arial',
      color: '#000000',
      bold: false,
      italic: false,
      underline: false,
      width: 200,
      height: 50,
      rotation: 0,
      opacity: 1,
      pageNumber: 1
    };

    const updatedEditors = [...this.editors, newEditor];
    this.editors = updatedEditors;
    this.editorsChanged.emit(updatedEditors);
    this.selectEditor(newEditor.id);
  }

  selectEditor(editorId: string): void {
    this.activeEditorId = editorId;
    this.selectedEditor = this.editors.find(e => e.id === editorId) || null;
    this.activeEditorChanged.emit(editorId);
  }

  deleteEditor(editorId: string): void {
    const updatedEditors = this.editors.filter(e => e.id !== editorId);
    this.editors = updatedEditors;
    this.editorsChanged.emit(updatedEditors);
    
    if (this.activeEditorId === editorId) {
      this.activeEditorId = null;
      this.selectedEditor = null;
      this.activeEditorChanged.emit(null);
    }
  }

  updateSelectedEditor(): void {
    if (this.selectedEditor) {
      const index = this.editors.findIndex(e => e.id === this.selectedEditor!.id);
      if (index !== -1) {
        this.editors[index] = { ...this.selectedEditor };
        this.editorsChanged.emit([...this.editors]);
      }
    }
  }

  onTextChange(text: string): void {
    if (this.selectedEditor) {
      this.selectedEditor.text = text;
      this.updateSelectedEditor();
    }
  }

  onFontSizeChange(fontSize: number): void {
    if (this.selectedEditor) {
      this.selectedEditor.fontSize = fontSize;
      this.updateSelectedEditor();
    }
  }

  onFontFamilyChange(fontFamily: string): void {
    if (this.selectedEditor) {
      this.selectedEditor.fontFamily = fontFamily;
      this.updateSelectedEditor();
    }
  }

  onColorChange(color: string): void {
    if (this.selectedEditor) {
      this.selectedEditor.color = color;
      this.updateSelectedEditor();
    }
  }

  toggleBold(): void {
    if (this.selectedEditor) {
      this.selectedEditor.bold = !this.selectedEditor.bold;
      this.updateSelectedEditor();
    }
  }

  toggleItalic(): void {
    if (this.selectedEditor) {
      this.selectedEditor.italic = !this.selectedEditor.italic;
      this.updateSelectedEditor();
    }
  }

  toggleUnderline(): void {
    if (this.selectedEditor) {
      this.selectedEditor.underline = !this.selectedEditor.underline;
      this.updateSelectedEditor();
    }
  }

  onOpacityChange(opacity: number): void {
    if (this.selectedEditor) {
      this.selectedEditor.opacity = opacity / 100;
      this.updateSelectedEditor();
    }
  }

  onRotationChange(rotation: number): void {
    if (this.selectedEditor) {
      this.selectedEditor.rotation = rotation;
      this.updateSelectedEditor();
    }
  }

  applyTextsToDocument(): void {
    this.applyTexts.emit([...this.editors]);
  }

  clearAllEditors(): void {
    this.editors = [];
    this.selectedEditor = null;
    this.activeEditorId = null;
    this.editorsChanged.emit([]);
    this.activeEditorChanged.emit(null);
  }
}