import { Component, Input, Output, EventEmitter, OnChanges, SimpleChanges, ChangeDetectorRef } from '@angular/core';

export interface ThumbnailItem {
  id: number;
  title: string;
  pageNumber: number;
  active: boolean;
  icon: string;
}

@Component({
  selector: 'app-shared-pdf-left-panel',
  templateUrl: './shared-pdf-left-panel.component.html',
  styleUrls: ['./shared-pdf-left-panel.component.scss'],
  standalone: false
})
export class SharedPdfLeftPanelComponent implements OnChanges {
  @Input() isVisible = false;
  @Input() thumbnails: ThumbnailItem[] = [];
  @Output() close = new EventEmitter<void>();
  @Output() thumbnailClick = new EventEmitter<ThumbnailItem>();

  constructor(private cdr: ChangeDetectorRef) {}

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['thumbnails']) {
      console.log('📸 좌측 패널 썸네일 업데이트:', this.thumbnails.length);
      this.cdr.detectChanges();
    }
  }

  onThumbnailClick(thumbnail: ThumbnailItem) {
    this.thumbnails.forEach(t => t.active = false);
    thumbnail.active = true;
    this.thumbnailClick.emit(thumbnail);
  }

  onClose() {
    this.close.emit();
  }
}
