import { Component, EventEmitter, Input, Output } from '@angular/core';
import { CommonModule, NgFor } from '@angular/common';

export interface RecentFile {
  name: string;
  lastModified: string;
  size: string;
}

@Component({
  selector: 'app-recent-files',
  standalone: true,
  imports: [CommonModule, NgFor],
  template: `
    <div class="recent-files-panel"
         (dragover)="onDragOver($event)"
         (dragenter)="onDragEnter($event)"
         (dragleave)="onDragLeave($event)"
         (drop)="onDrop($event)"
         [class.drag-over]="isDragOver">
      <div class="recent-title">최근파일</div>
      <div class="drag-overlay" *ngIf="isDragOver">
        <div class="drag-message">
          <svg width="48" height="48" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
            <rect x="3" y="3" width="18" height="18" rx="3" fill="#007acc"/>
            <text x="12" y="17" text-anchor="middle" fill="white" font-size="8" font-family="Arial" font-weight="bold">PDF</text>
          </svg>
          <p>PDF 파일을 여기에 드롭하세요</p>
        </div>
      </div>
      <div class="recent-table-wrap" *ngIf="files.length > 0; else noFiles">
        <table class="recent-files-table">
          <thead>
            <tr>
              <th style="width:50%">이름</th>
              <th style="width:30%">마지막 수정시간</th>
              <th style="width:15%">크기</th>
              <th style="width:5%"></th>
            </tr>
          </thead>
          <tbody>
            <tr *ngFor="let file of files">
              <td>
                <span class="file-name-cell">
                  <span class="pdf-svg">
                    <svg width="22" height="22" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                      <rect x="3" y="3" width="18" height="18" rx="3" fill="#F44336"/>
                      <text x="12" y="17" text-anchor="middle" fill="white" font-size="10" font-family="Arial" font-weight="bold">PDF</text>
                    </svg>
                  </span>
                    <span class="file-name-text" style="cursor:pointer;color:#1976d2;" (click)="onFileNameClick(file)">{{file.name}}</span>
                </span>
              </td>
              <td>{{file.lastModified}}</td>
              <td>{{file.size}}</td>
              <td>
                <button class="delete-btn" (click)="deleteFile.emit(file)" title="삭제">
                  <svg width="18" height="18" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M6 7h12M9 7V5a1 1 0 0 1 1-1h4a1 1 0 0 1 1 1v2m2 0v12a2 2 0 0 1-2 2H8a2 2 0 0 1-2-2V7h12z" stroke="#888" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
                  </svg>
                </button>
              </td>
            </tr>
          </tbody>
        </table>
        <input id="recent-file-input" type="file" accept="application/pdf" style="display:none" (change)="onRecentFileInputChange($event)" />
      </div>
      <ng-template #noFiles>
        <div class="no-files-message">
          <svg width="64" height="64" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M14,2H6A2,2 0 0,0 4,4V20A2,2 0 0,0 6,22H18A2,2 0 0,0 20,20V8L14,2M18,20H6V4H13V9H18V20Z" fill="#ddd"/>
          </svg>
          <p>최근 파일이 없습니다</p>
          <span>PDF 파일을 열면 여기에 표시됩니다</span>
        </div>
      </ng-template>
    </div>
  `,
  styles: [`
    .recent-files-panel {
      padding: 32px 24px;
      background: #fff;
      border-radius: 12px;
      box-shadow: 0 2px 8px rgba(0,0,0,0.03);
      height: 100%;
      display: flex;
      flex-direction: column;
      border: 2px dashed #ddd;
      transition: all 0.3s ease;
    }
    
    .recent-files-panel.drag-over {
      border-color: #007acc;
      background: rgba(0, 122, 204, 0.05);
    }
    
    .recent-title {
      font-size: 20px;
      font-weight: 600;
      color: #222;
      margin-bottom: 18px;
      flex-shrink: 0;
    }
    
    .recent-table-wrap {
      flex: 1;
      overflow-y: auto;
      overflow-x: hidden;
      min-height: 0;
      max-height: calc(100vh - 400px);
    }
    
    /* 커스텀 스크롤바 */
    .recent-table-wrap::-webkit-scrollbar {
      width: 8px;
    }
    
    .recent-table-wrap::-webkit-scrollbar-track {
      background: #f1f1f1;
      border-radius: 4px;
    }
    
    .recent-table-wrap::-webkit-scrollbar-thumb {
      background: #c1c1c1;
      border-radius: 4px;
    }
    
    .recent-table-wrap::-webkit-scrollbar-thumb:hover {
      background: #a8a8a8;
    }
    
    /* 반응형 스크롤 높이 조정 */
    @media (max-width: 768px) {
      .recent-table-wrap {
        max-height: calc(100vh - 350px);
      }
    }
    
    @media (max-width: 480px) {
      .recent-table-wrap {
        max-height: calc(100vh - 300px);
      }
    }
    
    .recent-files-table {
      width: 100%;
      border-collapse: separate;
      border-spacing: 0;
      background: #fff;
    }
    .recent-files-table th {
      color: #888;
      font-weight: 500;
      font-size: 15px;
      background: #fafafa;
      border-bottom: 2px solid #f0f0f0;
      padding: 12px 8px;
      text-align: left;
    }
    .recent-files-table td {
      color: #222;
      font-size: 15px;
      padding: 12px 8px;
      border-bottom: 1px solid #f5f5f5;
      vertical-align: middle;
    }
    .file-name-cell {
      display: flex;
      align-items: center;
    }
    .pdf-svg {
      margin-right: 10px;
      display: flex;
      align-items: center;
    }
    .file-name-text {
      font-weight: 500;
      color: #222;
    }
    .delete-btn {
      background: none;
      border: none;
      cursor: pointer;
      padding: 2px;
      border-radius: 4px;
      transition: background 0.15s;
    }
    .delete-btn:hover {
      background: #f5f5f5;
    }
    
    /* 빈 파일 메시지 */
    .no-files-message {
      flex: 1;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      text-align: center;
      color: #666;
      padding: 40px 20px;
    }
    
    .no-files-message svg {
      margin-bottom: 16px;
      opacity: 0.5;
    }
    
    .no-files-message p {
      font-size: 16px;
      font-weight: 500;
      margin: 0 0 8px 0;
      color: #333;
    }
    
    .no-files-message span {
      font-size: 14px;
      color: #666;
    }
    
    /* 드래그 앤 드롭 스타일 */
    .recent-files-panel.drag-over {
      border: 2px dashed #007acc;
      background: rgba(0, 122, 204, 0.05);
      position: relative;
    }
    
    .drag-overlay {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: rgba(0, 122, 204, 0.1);
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 12px;
      z-index: 1000;
    }
    
    .drag-message {
      text-align: center;
      color: #007acc;
    }
    
    .drag-message svg {
      margin-bottom: 12px;
    }
    
    .drag-message p {
      font-size: 16px;
      font-weight: 600;
      margin: 0;
    }
  `]
})
export class RecentFilesComponent {
  @Input() files: RecentFile[] = [];
  @Output() deleteFile = new EventEmitter<RecentFile>();
  // emit: File만 openFile로 전달
  @Output() openFile = new EventEmitter<File>();
  @Output() showSnackbar = new EventEmitter<string>();
  selectedRecentFile: RecentFile | null = null;
  
  // 드래그 앤 드롭 관련 속성
  isDragOver = false;
  private dragCounter = 0;

  onFileNameClick(file: RecentFile) {
    this.selectedRecentFile = file;
    const input = document.getElementById('recent-file-input') as HTMLInputElement;
    if (input) {
      input.value = '';
      input.click();
    }
  }

  onRecentFileInputChange(event: Event) {
    const input = event.target as HTMLInputElement;
    if (input.files && input.files.length > 0) {
      const fileObj = input.files[0];
      this.openFile.emit(fileObj);
    }
    this.selectedRecentFile = null;
  }
  
  // 드래그 앤 드롭 이벤트 핸들러
  onDragOver(event: DragEvent) {
    event.preventDefault();
    event.stopPropagation();
    
    // 파일이 드래그되고 있는지 확인
    if (event.dataTransfer?.types.includes('Files')) {
      event.dataTransfer.dropEffect = 'copy';
    }
  }
  
  onDragEnter(event: DragEvent) {
    event.preventDefault();
    event.stopPropagation();
    this.dragCounter++;
    
    // 파일이 드래그되고 있는지 확인
    if (event.dataTransfer?.types.includes('Files')) {
      this.isDragOver = true;
    }
  }
  
  onDragLeave(event: DragEvent) {
    event.preventDefault();
    event.stopPropagation();
    this.dragCounter--;
    
    // 모든 드래그 이벤트가 끝났을 때만 isDragOver를 false로 설정
    if (this.dragCounter <= 0) {
      this.isDragOver = false;
      this.dragCounter = 0;
    }
  }
  
  onDrop(event: DragEvent) {
    event.preventDefault();
    event.stopPropagation();
    this.isDragOver = false;
    this.dragCounter = 0;
    
    const files = event.dataTransfer?.files;
    if (files && files.length > 0) {
      const file = files[0];
      
      // 파일 타입 체크 (MIME 타입과 확장자 모두 확인)
      const isPdf = file.type === 'application/pdf' || file.name.toLowerCase().endsWith('.pdf');
      
      if (isPdf) {
        this.openFile.emit(file);
      } else {
        this.showSnackbar.emit('PDF 파일만 업로드할 수 있습니다.');
      }
    }
  }
}
