import { Component, Input, OnChanges, SimpleChanges, Output, EventEmitter } from '@angular/core';
import { AuthService } from '../../../../services/auth.service';

export interface TabData {
  id: number;
  name: string;
  active: boolean;
  pdfSrc: string | null;
}

@Component({
  selector: 'app-tab-bar',
  templateUrl: './tab-bar.component.html',
  styleUrls: ['./tab-bar.component.scss'],
  standalone: false
})
export class TabBarComponent implements OnChanges {
  @Input() currentFileName: string = '';
  @Input() currentPdfSrc: string | null = null;
  @Output() tabChanged = new EventEmitter<{ pdfSrc: string | null, fileName: string }>();

  tabs: TabData[] = [
    { id: 1, name: '새파일', active: true, pdfSrc: null }
  ];

  constructor(private authService: AuthService) {}

  get isLoggedIn(): boolean {
    return this.authService.isLoggedIn;
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes['currentFileName'] && changes['currentFileName'].currentValue) {
      this.updateCurrentTabName(changes['currentFileName'].currentValue);
    }
  }

  updateCurrentTabName(fileName: string) {
    const activeTab = this.tabs.find(tab => tab.active);
    if (activeTab) {
      activeTab.name = fileName;
    }
  }

  updateCurrentTabPdfSrc(pdfSrc: string | null) {
    const activeTab = this.tabs.find(tab => tab.active);
    if (activeTab) {
      activeTab.pdfSrc = pdfSrc;
    }
  }

  closeTab(tabId: number) {
    const wasActive = this.tabs.find(tab => tab.id === tabId)?.active;
    this.tabs = this.tabs.filter(tab => tab.id !== tabId);
    if (wasActive && this.tabs.length > 0) {
      this.tabs[0].active = true;
      this.tabChanged.emit({ pdfSrc: this.tabs[0].pdfSrc, fileName: this.tabs[0].name });
    }
  }

  addTab() {
    this.tabs.forEach(tab => tab.active = false);
    const newTab: TabData = {
      id: Date.now(),
      name: '새파일',
      active: true,
      pdfSrc: null
    };
    this.tabs.push(newTab);
    this.tabChanged.emit({ pdfSrc: null, fileName: '새파일' });
  }

  selectTab(tabId: number) {
    this.tabs.forEach(tab => {
      tab.active = tab.id === tabId;
    });
    const selected = this.tabs.find(tab => tab.id === tabId);
    this.tabChanged.emit({ pdfSrc: selected?.pdfSrc ?? null, fileName: selected?.name ?? '' });
  }

  // 파일 열기 시 App에서 호출
  setActiveTabPdf(fileName: string, pdfSrc: string | null) {
    const activeTab = this.tabs.find(tab => tab.active);
    if (activeTab) {
      activeTab.name = fileName;
      activeTab.pdfSrc = pdfSrc;
    }
  }

  onLogout(): void {
    this.authService.logout();
    // 모든 탭을 초기화
    this.tabs = [{ id: 1, name: '새파일', active: true, pdfSrc: null }];
    this.tabChanged.emit({ pdfSrc: null, fileName: '새파일' });
  }
}