import { Component, Input, Output, EventEmitter } from '@angular/core';

export type MenuPosition = 'left' | 'right';
export type MenuAction = 'toggle-sidebar' | 'toggle-properties' | 'zoom-in' | 'zoom-out' | 'ai-features' | 'translate' | 'edit-mode' | 'toggle-pdf-content';

export interface MenuItem {
  id: string;
  icon: string;
  tooltip: string;
  action: MenuAction;
  active?: boolean;
}

@Component({
  selector: 'app-vertical-menu',
  templateUrl: './vertical-menu.component.html',
  styleUrls: ['./vertical-menu.component.scss'],
  standalone: false
})
export class VerticalMenuComponent {
  @Input() position: MenuPosition = 'left';
  @Input() sidebarVisible = false;
  @Input() propertiesVisible = false;
  @Input() aiTranslateVisible = false;
  @Input() aiFeaturesVisible = false;
  @Input() pdfContentVisible = false;
  @Output() menuAction = new EventEmitter<MenuAction>();

  get leftMenuItems(): MenuItem[] {
    return [
      {
        id: 'page-panel-toggle',
        icon: '',
        tooltip: '페이지 패널 열기/닫기',
        action: 'toggle-sidebar',
        active: this.sidebarVisible
      }
    ];
  }

  get rightMenuItems(): MenuItem[] {
    return [
      {
        id: 'ai-features',
        icon: '',
        tooltip: 'AI 기능',
        action: 'ai-features',
        active: this.aiFeaturesVisible
      },
      {
        id: 'translate',
        icon: '',
        tooltip: 'AI 번역 기능',
        action: 'translate',
        active: this.aiTranslateVisible
      },
      {
        id: 'edit-mode',
        icon: '',
        tooltip: '편집 기능',
        action: 'toggle-properties',
        active: this.propertiesVisible
      }
    ];
  }

  get zoomItems(): MenuItem[] {
    return [
      {
        id: 'zoom-in',
        icon: '',
        tooltip: '확대',
        action: 'zoom-in'
      },
      {
        id: 'zoom-out',
        icon: '',
        tooltip: '축소',
        action: 'zoom-out'
      }
    ];
  }

  onMenuItemClick(item: MenuItem) {
    this.menuAction.emit(item.action);
  }
}
