import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  private isLoggedInSubject = new BehaviorSubject<boolean>(false);
  private isGuestModeSubject = new BehaviorSubject<boolean>(false);
  
  public isLoggedIn$ = this.isLoggedInSubject.asObservable();
  public isGuestMode$ = this.isGuestModeSubject.asObservable();

  constructor() {
    // 로컬 스토리지에서 로그인 상태 확인
    const savedLoginState = localStorage.getItem('isLoggedIn');
    const savedGuestMode = localStorage.getItem('guestMode');
    
    if (savedLoginState === 'true') {
      this.isLoggedInSubject.next(true);
    } else if (savedGuestMode === 'true') {
      this.isGuestModeSubject.next(true);
    }
  }

  get isLoggedIn(): boolean {
    return this.isLoggedInSubject.value || this.isGuestModeSubject.value;
  }

  login(): void {
    this.isLoggedInSubject.next(true);
    this.isGuestModeSubject.next(false);
    localStorage.setItem('isLoggedIn', 'true');
    localStorage.removeItem('guestMode');
  }

  async socialLogin(provider: string): Promise<boolean> {
    try {
      
      // 실제 소셜 로그인 API 호출 로직을 여기에 구현
      // 임시로 성공 처리
      await new Promise(resolve => setTimeout(resolve, 1000)); // 로딩 시뮬레이션
      
      this.isLoggedInSubject.next(true);
      this.isGuestModeSubject.next(false);
      localStorage.setItem('isLoggedIn', 'true');
      localStorage.setItem('loginProvider', provider);
      localStorage.removeItem('guestMode');
      
      return true;
    } catch (error) {
      return false;
    }
  }

  setGuestMode(isGuest: boolean): void {
    if (isGuest) {
      this.isGuestModeSubject.next(true);
      this.isLoggedInSubject.next(false);
      localStorage.setItem('guestMode', 'true');
      localStorage.removeItem('isLoggedIn');
    } else {
      this.isGuestModeSubject.next(false);
      localStorage.removeItem('guestMode');
    }
  }

  isGuest(): boolean {
    return this.isGuestModeSubject.value;
  }

  getLoginProvider(): string | null {
    return localStorage.getItem('loginProvider');
  }

  logout(): void {
    this.isLoggedInSubject.next(false);
    this.isGuestModeSubject.next(false);
    localStorage.removeItem('isLoggedIn');
    localStorage.removeItem('guestMode');
    localStorage.removeItem('loginProvider');
  }
}
