// (removed stray code)
import { Component, EventEmitter, Output, Input, ViewChild, ElementRef, OnChanges } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-top-menu-bar',
  templateUrl: './top-menu-bar.component.html',
  styleUrls: ['./top-menu-bar.component.scss'],
  standalone: false
})
export class TopMenuBarComponent implements OnChanges {
  @Input() saveMode: 'overlay' | 'textstream' = 'overlay';
  @Input() currentActiveMenu: string = '홈';
  @Input() pdfSrc: string | null = null; // PDF 파일 상태 추가
  @Output() showSnackbar = new EventEmitter<string>();
  
  // 드래그 관련 속성
  togglePosition = { x: 250, y: 10 }; // 초기 위치 (상단 메뉴바 내 기본 위치)
  isDragging = false;
  dragOffset = { x: 0, y: 0 };
  isSaving = false; // 저장 중 상태 추가
  private readonly TOGGLE_POSITION_KEY = 'saveModeTogglePosition';
  
  isSearching = false;
  searchResultCount: number | null = null;
  @Output() searchChange = new EventEmitter<string>();
  @Output() fileSelected = new EventEmitter<File>();
  @Output() createTextEditor = new EventEmitter<void>();
  @Output() createSentenceOverlay = new EventEmitter<void>(); // 문장 오버레이 생성 이벤트 추가
  @Output() saveOverlayedPdf = new EventEmitter<void>(); // PDF 저장 이벤트 추가
  @Output() downloadModifiedPdf = new EventEmitter<void>(); // PDF 다운로드 이벤트 추가
  @Output() printPdf = new EventEmitter<void>(); // PDF 인쇄 이벤트 추가
  @Output() saveModeChanged = new EventEmitter<'overlay' | 'textstream'>(); // 저장 모드 변경 이벤트 추가
  @Output() menuChanged = new EventEmitter<string>(); // 메뉴 변경 이벤트 추가
  @ViewChild('fileInput', { static: false }) fileInput!: ElementRef<HTMLInputElement>;
  searchQuery = '';
  menuItems = [
    { name: '홈', active: true },
    { name: '편집', active: false },
    { name: '파일변환', active: false },
    { name: '구성', active: false },
    { name: '보안', active: false }
  ];
  toolbarActions = [
    { icon: './images/ico-save@3x.png', tooltip: '저장', action: 'save' },
    { icon: './images/ico-print@3x.png', tooltip: '인쇄', action: 'print' },
    { icon: './images/ico-mail@3x.png', tooltip: '이메일', action: 'email' },
    { icon: './images/ico-more@3x.png', tooltip: '더보기', action: 'more' },
    { icon: './images/ico-before@3x.png', tooltip: '실행취소', action: 'undo' },
    { icon: './images/ico-after@3x.png', tooltip: '다시실행', action: 'redo' },
    { icon: './images/btn-top-ai@3x.png', tooltip: 'AI 무협지 생성', action: 'novel-generator' }
  ];

  constructor(private router: Router) {}

  public onToolbarAction(action: string) {
    if (action === 'open') {
      this.openFileDialog();
    } else if (action === 'save') {
      // 수정된 PDF 다운로드 이벤트 발생
      this.downloadModifiedPdf.emit();
    } else if (action === 'print') {
      // PDF 인쇄 이벤트 발생
      this.printPdf.emit();
    } else if (action === 'novel-generator') {
      // AI 무협지 생성 페이지로 이동
      this.router.navigate(['/novel']);
    }
    // 실제 액션 구현
  }
  
  public goToPdfPage() {
    this.router.navigate(['/pdf']);
  }
  
  public openFileDialog() {
    this.fileInput.nativeElement.click();
  }

  public onFileSelected(event: Event) {
    const input = event.target as HTMLInputElement;
    if (input.files && input.files.length > 0) {
      const file = input.files[0];
      if (file.type === 'application/pdf') {
        this.fileSelected.emit(file);
      } else {
        this.showSnackbar.emit('PDF 파일만 선택해주세요.');
      }
    }
  }

  public onSearchChange(query: string) {
    this.searchQuery = query;
    this.isSearching = true;
    this.searchResultCount = null;
    this.searchChange.emit(query);
  }

  public onMenuItemClick(item: any) {
    // PDF 파일이 필요한 메뉴들
    const requiresPdfMenus = ['편집', '파일변환', '구성', '보안'];
    
    // PDF 파일이 없을 때 해당 메뉴 클릭 차단
    if (requiresPdfMenus.includes(item.name) && !this.pdfSrc) {
      // 메뉴 활성화하지 않고 이벤트만 발생시켜서 상위에서 스낵바 처리
      this.menuChanged.emit(item.name);
      return;
    }

    // 메뉴 활성화 처리
    this.menuItems.forEach(menu => menu.active = false);
    item.active = true;
    
    // 메뉴 변경 이벤트 발생
    this.menuChanged.emit(item.name);
    
    // 편집 메뉴 클릭 시 문장 단위 오버레이 생성
    if (item.name === '편집') {
      this.createSentenceOverlay.emit();
    }
    
    // 구성 메뉴는 menuChanged 이벤트에서만 처리하도록 변경
    // pageReorderRequested 이벤트는 제거 (중복 처리 방지)
  }

  public onSearchEnter() {
    this.isSearching = true;
    this.searchResultCount = null;
    this.searchChange.emit(this.searchQuery);
  }

  public setSearchResultCount(count: number) {
    this.searchResultCount = count;
  }

  public ngOnInit() {
    // 저장된 토글 위치 로드
    this.loadTogglePosition();
    
    // 저장 완료 이벤트 리스너 등록
    document.addEventListener('pdf-save-completed', () => {
      this.isSaving = false;
    });
    
    // 검색 완료 이벤트 리스너 등록
    document.addEventListener('search-finished', () => {
      this.isSearching = false;
    });
    // 검색 결과 개수 이벤트 리스너 등록
    document.addEventListener('search-result-count', (e: any) => {
      this.searchResultCount = e.detail.count;
      this.isSearching = false;
    });
  }

  ngOnChanges() {
    // currentActiveMenu가 변경될 때 메뉴 아이템들의 active 상태 업데이트
    this.updateMenuActiveState();
  }

  private updateMenuActiveState() {
    this.menuItems.forEach(menu => {
      menu.active = menu.name === this.currentActiveMenu;
    });
  }

  onSaveModeChange(mode: 'overlay' | 'textstream'): void {
    // 드래그 중일 때는 버튼 클릭 무시
    if (this.isDragging) return;
    
    this.saveModeChanged.emit(mode);
  }

  onSavePdf(): void {
    // 드래그 중일 때는 버튼 클릭 무시
    if (this.isDragging || this.isSaving) return;
    
    this.isSaving = true;
    
    // PDF 저장 이벤트 발생
    this.saveOverlayedPdf.emit();
    
    // 저장 완료 후 상태 리셋 (3초 후)
    setTimeout(() => {
      this.isSaving = false;
    }, 3000);
  }

  isMenuDisabled(menuName: string): boolean {
    const requiresPdfMenus = ['편집', '파일변환', '구성', '보안'];
    return requiresPdfMenus.includes(menuName) && !this.pdfSrc;
  }

  // 드래그 시작
  onDragStart(event: MouseEvent): void {
    event.preventDefault();
    this.isDragging = true;
    
    const rect = (event.target as HTMLElement).closest('.save-mode-toggle')?.getBoundingClientRect();
    if (rect) {
      this.dragOffset.x = event.clientX - rect.left;
      this.dragOffset.y = event.clientY - rect.top;
    }
    
    document.addEventListener('mousemove', this.onDragMove.bind(this));
    document.addEventListener('mouseup', this.onDragEnd.bind(this));
  }

  // 드래그 이동
  onDragMove(event: MouseEvent): void {
    if (!this.isDragging) return;
    
    const parentRect = document.querySelector('.top-menu-bar')?.getBoundingClientRect();
    if (!parentRect) return;
    
    let newX = event.clientX - parentRect.left - this.dragOffset.x;
    let newY = event.clientY - parentRect.top - this.dragOffset.y;
    
    // 상단 메뉴바 범위 내로 제한
    const toggleWidth = 280; // 대략적인 토글 버튼 너비
    const toggleHeight = 40; // 대략적인 토글 버튼 높이
    
    newX = Math.max(0, Math.min(newX, parentRect.width - toggleWidth));
    newY = Math.max(0, Math.min(newY, parentRect.height - toggleHeight));
    
    this.togglePosition.x = newX;
    this.togglePosition.y = newY;
  }

  // 드래그 종료
  onDragEnd(): void {
    this.isDragging = false;
    document.removeEventListener('mousemove', this.onDragMove.bind(this));
    document.removeEventListener('mouseup', this.onDragEnd.bind(this));
    
    // 위치 저장
    this.saveTogglePosition();
  }

  // 토글 위치 저장
  private saveTogglePosition(): void {
    localStorage.setItem(this.TOGGLE_POSITION_KEY, JSON.stringify(this.togglePosition));
  }

  // 토글 위치 로드
  private loadTogglePosition(): void {
    const saved = localStorage.getItem(this.TOGGLE_POSITION_KEY);
    if (saved) {
      try {
        this.togglePosition = JSON.parse(saved);
      } catch {
        // 파싱 실패 시 기본값 유지
      }
    }
  }
}
