import { Component, OnInit, OnDestroy, inject } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { DebugLoggerService, DebugLog } from '../../services/debug-logger.service';
import { SnackbarService } from '../../../services/snackbar.service';
import { IframeDetectorService } from '../../../services/iframe-detector.service';

@Component({
  selector: 'app-debug-console',
  standalone: true,
  imports: [CommonModule],
  template: `
    <!-- iframe 내부에서는 디버그 콘솔 숨김 -->
    <div class="debug-console-overlay" 
         *ngIf="isVisible && !isInsideIframe"
         (mousedown)="onOverlayMouseDown($event)">
      <div class="debug-console"
           [style.left.px]="position.x"
           [style.top.px]="position.y"
           [style.width.px]="size.width"
           [style.height.px]="size.height"
           (mousedown)="$event.stopPropagation()">
        
        <!-- 헤더 (드래그 가능) -->
        <div class="debug-header"
             (mousedown)="onHeaderMouseDown($event)">
          <div class="debug-title">
            <span class="debug-icon">🎚️</span>
            <span class="debug-title-text" *ngIf="size.width > 250">Debug Console</span>
            <span class="debug-count" *ngIf="size.width > 250">({{ logs.length }})</span>
          </div>
          <div class="debug-actions">
            <button class="debug-btn" (click)="toggleAutoScroll()" [title]="autoScroll ? '자동 스크롤 끄기' : '자동 스크롤 켜기'" *ngIf="size.width > 200">
              <span class="btn-icon">{{ autoScroll ? '▼' : '⏸' }}</span>
            </button>
            <button class="debug-btn" (click)="clearLogs()" title="로그 지우기" *ngIf="size.width > 200">
              <span class="btn-icon">×</span>
            </button>
            <button class="debug-btn" (click)="copyLogs()" title="로그 복사" *ngIf="size.width > 200">
              <span class="btn-icon">⎘</span>
            </button>
            <button class="debug-btn" (click)="close()" title="닫기">
              <span class="btn-icon">✕</span>
            </button>
          </div>
        </div>

        <!-- 로그 내용 -->
        <div class="debug-content" #logContainer>
          <div class="debug-log" 
               *ngFor="let log of logs; trackBy: trackByIndex"
               [class.log-info]="log.level === 'info'"
               [class.log-warn]="log.level === 'warn'"
               [class.log-error]="log.level === 'error'"
               [class.log-success]="log.level === 'success'">
            <span class="log-time">{{ formatTime(log.timestamp) }}</span>
            <span class="log-icon">{{ getIcon(log.level) }}</span>
            <span class="log-category">[{{ log.category }}]</span>
            <span class="log-message">{{ log.message }}</span>
          </div>
          <div class="debug-empty" *ngIf="logs.length === 0">
            로그가 없습니다.
          </div>
        </div>

        <!-- 리사이즈 핸들 -->
        <div class="resize-handle" 
             (mousedown)="onResizeMouseDown($event)">
        </div>
      </div>
    </div>
  `,
  styles: [`
    .debug-console-overlay {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 9999;
      pointer-events: none;
    }

    .debug-console {
      position: fixed;
      background: rgba(26, 26, 46, 0.75);
      border: 1px solid rgba(83, 168, 182, 0.6);
      border-radius: 16px;
      box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3),
                  0 0 0 1px rgba(255, 255, 255, 0.15) inset;
      display: flex;
      flex-direction: column;
      font-family: 'Consolas', 'Monaco', 'Courier New', monospace;
      overflow: hidden;
      pointer-events: auto;
    }

    .debug-header {
      background: rgba(15, 52, 96, 0.7);
      border-bottom: 1px solid rgba(83, 168, 182, 0.5);
      padding: 6px 8px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      cursor: move;
      user-select: none;
    }

    .debug-title {
      display: flex;
      align-items: center;
      gap: 6px;
      color: #87ceeb;
      font-weight: bold;
      font-size: 12px;
      min-width: 0;
      flex-shrink: 1;
    }

    .debug-title-text {
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    .debug-icon {
      font-size: 14px;
      filter: drop-shadow(0 0 2px rgba(135, 206, 235, 0.8));
      animation: wiggle 2s infinite;
    }

    @keyframes wiggle {
      0%, 100% { transform: rotate(0deg); }
      25% { transform: rotate(-5deg); }
      75% { transform: rotate(5deg); }
    }

    .debug-count {
      color: #e94560;
      font-size: 11px;
      font-weight: normal;
      background: rgba(233, 69, 96, 0.2);
      padding: 2px 6px;
      border-radius: 10px;
    }

    .debug-actions {
      display: flex;
      gap: 3px;
      flex-shrink: 0;
    }

    .debug-btn {
      background: rgba(255, 255, 255, 0.1);
      border: 1px solid rgba(255, 255, 255, 0.2);
      border-radius: 4px;
      padding: 3px 5px;
      cursor: pointer;
      font-size: 12px;
      transition: all 0.2s;
      color: #ffffff;
      min-width: 24px;
      height: 24px;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    .debug-btn:hover {
      background: rgba(255, 255, 255, 0.2);
      border-color: rgba(255, 255, 255, 0.4);
      transform: scale(1.05);
    }

    .btn-icon {
      color: #ffffff;
      font-weight: bold;
      font-size: 14px;
      line-height: 1;
      display: block;
    }

    .debug-content {
      flex: 1;
      overflow-y: auto;
      padding: 6px;
      background: transparent;
      color: #e8e8e8;
      font-size: 10px;
      line-height: 1.4;
      scrollbar-width: none; /* Firefox */
      -ms-overflow-style: none; /* IE and Edge */
    }

    .debug-content::-webkit-scrollbar {
      display: none; /* Chrome, Safari, Opera */
    }

    .debug-log {
      display: flex;
      gap: 6px;
      padding: 4px 0;
      border-bottom: 1px solid rgba(83, 168, 182, 0.1);
      transition: background 0.2s;
    }

    .debug-log:hover {
      background: rgba(83, 168, 182, 0.1);
      padding-left: 4px;
      margin-left: -4px;
      border-radius: 4px;
    }

    .debug-log:last-child {
      border-bottom: none;
    }

    .log-time {
      color: #858585;
      flex-shrink: 0;
      width: 70px;
      font-size: 10px;
    }

    .log-icon {
      flex-shrink: 0;
      width: 18px;
      font-size: 12px;
    }

    .log-category {
      color: #53a8b6;
      font-weight: bold;
      flex-shrink: 0;
      min-width: 90px;
      font-size: 11px;
    }

    .log-message {
      color: #d4d4d4;
      word-break: break-word;
      font-size: 11px;
    }

    .log-info .log-message {
      color: #4fc3f7;
    }

    .log-warn .log-message {
      color: #ffb74d;
    }

    .log-error .log-message {
      color: #e57373;
    }

    .log-success .log-message {
      color: #81c784;
    }

    .debug-empty {
      text-align: center;
      color: #666;
      padding: 30px;
      font-style: italic;
      font-size: 12px;
    }

    .resize-handle {
      position: absolute;
      right: 0;
      bottom: 0;
      width: 20px;
      height: 20px;
      cursor: nwse-resize;
      background: linear-gradient(135deg, transparent 0%, transparent 50%, #53a8b6 50%, #53a8b6 100%);
      border-radius: 0 0 14px 0;
      opacity: 0.6;
      transition: opacity 0.2s, transform 0.2s;
      z-index: 10;
    }

    .resize-handle:hover {
      opacity: 1;
      background: linear-gradient(135deg, transparent 0%, transparent 50%, #e94560 50%, #e94560 100%);
      transform: scale(1.1);
    }

    .resize-handle::before {
      content: '⋰';
      position: absolute;
      right: 2px;
      bottom: 0px;
      color: rgba(255, 255, 255, 0.8);
      font-size: 14px;
      font-weight: bold;
      pointer-events: none;
    }
  `]
})
export class DebugConsoleComponent implements OnInit, OnDestroy {
  private destroy$ = new Subject<void>();
  private iframeDetector = inject(IframeDetectorService);
  
  isVisible = false;
  isInsideIframe = false;
  logs: DebugLog[] = [];
  autoScroll = true;
  
  private hasUserManuallyClosed = false; // 사용자가 수동으로 닫았는지 추적

  // 드래그 상태
  private isDragging = false;
  private dragStartX = 0;
  private dragStartY = 0;

  // 리사이즈 상태
  private isResizing = false;
  private resizeStartX = 0;
  private resizeStartY = 0;

  // 위치 및 크기
  position = { x: 100, y: 100 };
  size = { width: 600, height: 400 };

  constructor(
    private debugLogger: DebugLoggerService,
    private snackbar: SnackbarService
  ) {
    // iframe 감지
    this.isInsideIframe = this.iframeDetector.isInsideIframe();
    
    // localStorage에서 콘솔 표시 상태 복원
    const savedVisibility = localStorage.getItem('debugConsoleVisible');
    const userManuallyClosed = localStorage.getItem('debugConsoleUserClosed');
    
    // 사용자가 수동으로 닫은 적이 있으면 그 상태 유지
    if (userManuallyClosed === 'true') {
      this.hasUserManuallyClosed = true;
      this.isVisible = false;
    } else if (savedVisibility === 'true') {
      this.isVisible = true;
    }
    
    // localStorage에서 위치 및 크기 복원
    const savedPosition = localStorage.getItem('debugConsolePosition');
    const savedSize = localStorage.getItem('debugConsoleSize');
    
    if (savedPosition) {
      try {
        this.position = JSON.parse(savedPosition);
      } catch (e) {}
    }
    
    if (savedSize) {
      try {
        this.size = JSON.parse(savedSize);
      } catch (e) {}
    }
  }

  ngOnInit(): void {
    // 로그 구독
    this.debugLogger.logs$
      .pipe(takeUntil(this.destroy$))
      .subscribe(logs => {
        this.logs = logs;
        if (this.autoScroll) {
          setTimeout(() => this.scrollToBottom(), 10);
        }
      });

    // 디버그 모드 상태 구독 - 활성화되면 자동으로 콘솔 표시 (단, 사용자가 수동으로 닫은 경우 제외)
    this.debugLogger.enabled$
      .pipe(takeUntil(this.destroy$))
      .subscribe(enabled => {
        if (enabled && !this.hasUserManuallyClosed) {
          this.isVisible = true;
          localStorage.setItem('debugConsoleVisible', 'true');
        } else if (!enabled) {
          // 디버그 모드가 꺼지면 콘솔도 숨김
          this.isVisible = false;
          this.hasUserManuallyClosed = false;
          localStorage.removeItem('debugConsoleUserClosed');
        }
      });

    // 초기 위치 설정 (저장된 위치가 없을 때만 화면 중앙으로)
    const savedPosition = localStorage.getItem('debugConsolePosition');
    if (!savedPosition) {
      this.centerWindow();
    } else {
      // 저장된 위치가 있으면 화면 내에 있는지만 확인
      this.ensureVisible();
    }

    // 윈도우 리사이즈 시 위치 조정
    window.addEventListener('resize', () => this.ensureVisible());
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  /**
   * 콘솔 표시
   */
  show(): void {
    this.isVisible = true;
    this.hasUserManuallyClosed = false;
    localStorage.setItem('debugConsoleVisible', 'true');
    localStorage.removeItem('debugConsoleUserClosed');
    
    // DebugLogger 상태도 동기화
    this.debugLogger.setEnabled(true);
  }

  /**
   * 콘솔 숨기기
   */
  close(): void {
    this.isVisible = false;
    this.hasUserManuallyClosed = true;
    localStorage.setItem('debugConsoleVisible', 'false');
    localStorage.setItem('debugConsoleUserClosed', 'true');
    
    // DebugLogger 상태도 동기화
    this.debugLogger.setEnabled(false);
  }

  /**
   * 콘솔 토글
   */
  toggle(): void {
    if (this.isVisible) {
      this.close();
    } else {
      this.show();
    }
  }

  /**
   * 로그 지우기
   */
  clearLogs(): void {
    this.debugLogger.clear();
    this.snackbar.showInfo('로그를 지웠습니다.');
  }

  /**
   * 로그 복사
   */
  copyLogs(): void {
    const text = this.debugLogger.exportAsText();
    navigator.clipboard.writeText(text).then(() => {
      this.snackbar.showSuccess('로그를 클립보드에 복사했습니다!');
    }).catch(err => {
      console.warn('복사 실패:', err);
      this.snackbar.showError('로그 복사에 실패했습니다.');
    });
  }

  /**
   * 자동 스크롤 토글
   */
  toggleAutoScroll(): void {
    this.autoScroll = !this.autoScroll;
    if (this.autoScroll) {
      this.scrollToBottom();
    }
  }

  /**
   * 오버레이 클릭 (닫기)
   */
  onOverlayMouseDown(event: MouseEvent): void {
    if (event.target === event.currentTarget) {
      this.close();
    }
  }

  /**
   * 헤더 드래그 시작
   */
  onHeaderMouseDown(event: MouseEvent): void {
    if (event.button !== 0) return; // 왼쪽 버튼만

    this.isDragging = true;
    this.dragStartX = event.clientX - this.position.x;
    this.dragStartY = event.clientY - this.position.y;

    document.addEventListener('mousemove', this.onMouseMove);
    document.addEventListener('mouseup', this.onMouseUp);
    
    event.preventDefault();
  }

  /**
   * 리사이즈 시작
   */
  onResizeMouseDown(event: MouseEvent): void {
    if (event.button !== 0) return;

    this.isResizing = true;
    this.resizeStartX = event.clientX;
    this.resizeStartY = event.clientY;

    document.addEventListener('mousemove', this.onMouseMove);
    document.addEventListener('mouseup', this.onMouseUp);
    
    event.preventDefault();
    event.stopPropagation();
  }

  /**
   * 마우스 이동
   */
  private onMouseMove = (event: MouseEvent): void => {
    if (this.isDragging) {
      this.position.x = event.clientX - this.dragStartX;
      this.position.y = event.clientY - this.dragStartY;
      this.ensureVisible();
    } else if (this.isResizing) {
      const deltaX = event.clientX - this.resizeStartX;
      const deltaY = event.clientY - this.resizeStartY;

      this.size.width = Math.max(120, this.size.width + deltaX);
      this.size.height = Math.max(100, this.size.height + deltaY);

      this.resizeStartX = event.clientX;
      this.resizeStartY = event.clientY;
      
      this.ensureVisible();
    }
  };

  /**
   * 마우스 업
   */
  private onMouseUp = (): void => {
    this.isDragging = false;
    this.isResizing = false;
    
    // 위치와 크기를 localStorage에 저장
    localStorage.setItem('debugConsolePosition', JSON.stringify(this.position));
    localStorage.setItem('debugConsoleSize', JSON.stringify(this.size));
    
    document.removeEventListener('mousemove', this.onMouseMove);
    document.removeEventListener('mouseup', this.onMouseUp);
  };

  /**
   * 화면 내 위치 보장
   */
  private ensureVisible(): void {
    const margin = 20;
    const maxX = window.innerWidth - this.size.width - margin;
    const maxY = window.innerHeight - this.size.height - margin;

    this.position.x = Math.max(margin, Math.min(this.position.x, maxX));
    this.position.y = Math.max(margin, Math.min(this.position.y, maxY));
  }

  /**
   * 화면 중앙 배치
   */
  private centerWindow(): void {
    this.position.x = (window.innerWidth - this.size.width) / 2;
    this.position.y = (window.innerHeight - this.size.height) / 2;
  }

  /**
   * 맨 아래로 스크롤
   */
  private scrollToBottom(): void {
    const content = document.querySelector('.debug-content');
    if (content) {
      content.scrollTop = content.scrollHeight;
    }
  }

  /**
   * 시간 포맷
   */
  formatTime(date: Date): string {
    return date.toLocaleTimeString('ko-KR', {
      hour: '2-digit',
      minute: '2-digit',
      second: '2-digit',
      hour12: false
    });
  }

  /**
   * 레벨별 아이콘
   */
  getIcon(level: DebugLog['level']): string {
    switch (level) {
      case 'info': return 'ℹ️';
      case 'warn': return '⚠️';
      case 'error': return '❌';
      case 'success': return '✅';
    }
  }

  /**
   * TrackBy 함수
   */
  trackByIndex(index: number): number {
    return index;
  }
}
