import { Component, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-properties-panel',
  templateUrl: './properties-panel.component.html',
  styleUrls: ['./properties-panel.component.scss'],
  standalone: false
})
export class PropertiesPanelComponent {
  @Input() isVisible = false;
  @Output() close = new EventEmitter<void>();

  fontProperties = {
    type: 'text',
    size: 30,
    color: '#000000',
    bold: true,
    italic: false,
    underline: false,
    strikethrough: false,
    align: 'left'
  };

  spacingProperties = {
    letterSpacing: 0,
    lineHeight: 10,
    paragraphSpacing: 0
  };

  fontTypes = [
    { value: 'text', label: '텍스트' },
    { value: 'heading', label: '제목' },
    { value: 'caption', label: '캡션' }
  ];

  alignments = [
    { value: 'left', icon: 'align-left', tooltip: '왼쪽 정렬' },
    { value: 'center', icon: 'align-center', tooltip: '가운데 정렬' },
    { value: 'right', icon: 'align-right', tooltip: '오른쪽 정렬' },
    { value: 'justify', icon: 'align-justify', tooltip: '양쪽 정렬' }
  ];

  actions = [
    { name: 'undo', tooltip: '되돌리기' },
    { name: 'redo', tooltip: '다시하기' },
    { name: 'flip-horizontal', tooltip: '좌우반전' },
    { name: 'flip-vertical', tooltip: '상하반전' },
    { name: 'crop', tooltip: '자르기' },
    { name: 'convert-image', tooltip: '이미지 변환' }
  ];

  onClose() {
    this.close.emit();
  }

  onFontStyleToggle(style: 'bold' | 'italic' | 'underline' | 'strikethrough') {
    this.fontProperties[style] = !this.fontProperties[style];
  }

  onAlignmentChange(alignment: string) {
    this.fontProperties.align = alignment;
  }

  onAction(action: string) {
    // 실제 액션 구현
  }
}
