import { Component, Input, Output, EventEmitter, OnChanges, SimpleChanges, AfterViewInit, ChangeDetectorRef } from '@angular/core';

declare var pdfjsLib: any;

export interface ThumbnailItem {
  id: number;
  title: string;
  pageNumber: number;
  active: boolean;
  thumbnailUrl?: string; // PDF 페이지 썸네일 URL
}

@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.scss'],
  standalone: false
})
export class SidebarComponent implements OnChanges, AfterViewInit {
  @Input() isVisible = true;
  @Input() pdfSrc: string | null = null; // PDF 소스
  @Input() totalPages: number = 0; // 전체 페이지 수
  @Input() currentPage: number = 1; // 현재 페이지
  @Output() close = new EventEmitter<void>();
  @Output() thumbnailClick = new EventEmitter<ThumbnailItem>();

  thumbnails: ThumbnailItem[] = [];
  private pdfDocument: any = null;

  constructor(private cdr: ChangeDetectorRef) {}

  ngAfterViewInit() {
    // PDF.js 초기화
    if (typeof pdfjsLib !== 'undefined') {
      pdfjsLib.GlobalWorkerOptions.workerSrc = 'https://cdnjs.cloudflare.com/ajax/libs/pdf.js/3.11.174/pdf.worker.min.js';
    }
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes['totalPages'] && this.totalPages > 0) {
      this.generateThumbnails();
    }
    if (changes['currentPage']) {
      this.updateActivePageThumbnail();
    }
    if (changes['pdfSrc'] && this.pdfSrc) {
      this.loadPdfForThumbnails();
    }
  }

  private async loadPdfForThumbnails() {
    if (!this.pdfSrc || typeof pdfjsLib === 'undefined') {
      return;
    }

    try {
      this.pdfDocument = await pdfjsLib.getDocument(this.pdfSrc).promise;
      this.generateThumbnailImages();
    } catch (error) {
    }
  }

  private async generateThumbnailImages() {
    if (!this.pdfDocument || this.thumbnails.length === 0) {
      return;
    }

    for (let i = 0; i < this.thumbnails.length; i++) {
      const thumbnail = this.thumbnails[i];
      try {
        const page = await this.pdfDocument.getPage(thumbnail.pageNumber);
        const thumbnailUrl = await this.createThumbnailCanvas(page);
        thumbnail.thumbnailUrl = thumbnailUrl;
      } catch (error) {
      }
    }
    this.cdr.detectChanges();
  }

  private async createThumbnailCanvas(page: any): Promise<string> {
    const viewport = page.getViewport({ scale: 0.2 }); // 작은 스케일로 썸네일 생성
    const canvas = document.createElement('canvas');
    const context = canvas.getContext('2d');
    
    canvas.height = viewport.height;
    canvas.width = viewport.width;

    const renderContext = {
      canvasContext: context,
      viewport: viewport
    };

    await page.render(renderContext).promise;
    return canvas.toDataURL();
  }

  private generateThumbnails() {
    this.thumbnails = [];
    for (let i = 1; i <= this.totalPages; i++) {
      this.thumbnails.push({
        id: i,
        title: `페이지 ${i}`,
        pageNumber: i,
        active: i === this.currentPage
      });
    }
  }

  private updateActivePageThumbnail() {
    this.thumbnails.forEach(t => t.active = (t.pageNumber === this.currentPage));
  }

  onThumbnailClick(thumbnail: ThumbnailItem) {
    this.thumbnails.forEach(t => t.active = false);
    thumbnail.active = true;
    this.thumbnailClick.emit(thumbnail);
  }

  onClose() {
    this.close.emit();
  }
}
